/* darkmode */

body {
  font-family: "Libre Franklin", sans-serif;
}

.light-theme {
  position: relative;
  background: #f0f0f0 !important;
  font-family: "Libre Franklin", sans-serif;
}

ul,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-bottom: 0px;
}

a {
  text-decoration: none;
}

.light-theme .page-sidebar {
  border-radius: 20px;
  background: linear-gradient(180deg, #236115 0%, #6fb95d 100%);
  width: 210px;
  margin: 20px 0px 20px 20px;
  position: fixed;
  height: 95%;
  transition: all 0.4s;
}

.page-header {
  position: fixed;
  width: calc(100% - 230px);
  z-index: 9;
  padding: 20px 25px;
  background: #f0f0f0 !important;
  transition: all 0.4s;

}

.page-container {
  margin-left: 245px;
  width: 100%;
  position: relative;
  transition: all 0.4s;
}

.page-content {
  padding: 0px 25px 50px 25px;
}

.main-wrapper {
  position: relative;
  top: 115px;
  z-index: 1;
}

.compact-sidebar .page-header {
  position: fixed;
  width: calc(100% - 100px);
  z-index: 9;
  padding: 20px 25px;
  // background: #f0f0f0;
  transition: all 0.4s;
}

.compact-sidebar .dash_header {
  width: calc(100% - 430px);
  padding: 20px 0px;

}

.dash_header {
  width: calc(100% - 560px);
  padding: 20px 0px;
}

#theme-text {
  margin-right: 8px;
}

button.btn.btn-secondary.dropdown-toggle {
  background: none;
}

.compact-sidebar .page-container {
  margin-left: 115px;
  transition: all 0.4s;
}

.compact-sidebar .page-sidebar {
  width: 80px;
  position: fixed;
  box-shadow: none;
  transition: all 0.4s;
  height: 95%;
  z-index: 2;
}

.compact-sidebar .page-sidebar:before {
  position: fixed;
  height: 95%;
  width: 80px;
  display: block;
  content: "";
  box-shadow: 0 0 1.25rem rgba(31, 45, 61, 0.04);
  z-index: -1;
}

.toggle-padding {
  padding: 4px 8px !important;
  border-radius: 4px !important;
}




.compact-sidebar .sidebar_menu_span {
